/* src/components/SearchResults/SearchResults.css */

.pagination {
  display: flex;
  list-style: none;
  padding: 0;
}

.pagination li {
  margin-right: 5px;
}

.pagination a {
  text-decoration: none;
  padding: 8px 12px;
  border: 1px solid #ddd;
  color: #333;
  cursor: pointer;
}

.pagination a:hover {
  background-color: #eee;
}

.pagination .active a {
  background-color: #007bff;
  color: #fff;
}
